/* Timeline vertical line */
ul.timeline::before {
  content: " ";
  background: #fff;
  display: inline-block;
  position: absolute;
  left: 2rem;
  width: 7px;
  height: 100%;
  border-radius: 1rem;
}

ul.timeline {
  list-style-type: none;
}

li.timelineItem {
  margin: 20px 0;
}

/* Timeline item arrow */
.timelineArrow {
  border-top: 0.5rem solid transparent;
  border-right: 0.5rem solid #fff;
  border-bottom: 0.5rem solid transparent;
  display: block;
  position: absolute;
  left: 3rem;
}

/* Timeline item circle marker */
li.timelineItem::before {
  content: " ";
  background: #ddd;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 3px solid #636fa4;
  left: 1.8rem;
  width: 14px;
  height: 14px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
