/* Dark and Light theme toggle */
.light-mode {
  background-color: #c4d6de;
  transition: background-color 0.25s ease-out;
}

.light-mode nav {
  transition: background-color 0.25s ease-out;
}

.dark-mode {
  background-color: #1a1919;
  color: #cecece;
  transition: background-color 0.25s ease-out;
}

.dark-mode nav {
  transition: background-color 0.25s ease-out;
}

.dark-mode hr {
  background-color: #999;
}

.toggle-container {
  margin: 4px;
}

.toggle {
  padding: 0 2px;
}

input[type="checkbox"].checkbox {
  width: 50px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
}

input[type="checkbox"].checkbox:checked + label {
  left: 35px;
}

input[type="checkbox"].checkbox:focus-visible {
  outline: solid 2px white;
}

input[type="checkbox"].checkbox + label {
  display: inline-block;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
  position: absolute;
  left: 2px;
  background-color: #ef4339;
}